import isEmpty from "../validation/isEmpty";

export const isQueryStringSearchable = (queryString) => {
    const queryParams = new URLSearchParams(queryString);
    const paramKeys = [];
    for (let key of queryParams.keys()) {
        paramKeys.push(key);
    }
    console.log(paramKeys)
    if (
        (paramKeys.includes('start') &&
            paramKeys.includes('end') &&
            paramKeys.length === 2) ||
        isEmpty(queryString)
    ) {
        console.log('not searchable')
        return false;
    } else {
        console.log('searchable')
        return true
    }
}

export const getItemsSearchFilterObject = (props) => {
    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString) || '';
    let searchFilterObject = {}
    //return empty of no query string
    if (!isQueryStringSearchable(queryString)) {
        return searchFilterObject
    }
    const itemName = queryParams.get('itemName');
    const matchExactly = queryParams.get('matchExactly');
    const barcode = queryParams.get('barcode');
    const category = queryParams.get('category');
    // const categoryName = queryParams.get('categoryName');
    const weightFrom = queryParams.get('weightFrom');
    const weightUpto = queryParams.get('weightUpto');
    const cpFrom = queryParams.get('cpFrom');
    const cpUpto = queryParams.get('cpUpto');
    const stockFrom = queryParams.get('stockFrom');
    const stockUpto = queryParams.get('stockUpto');
    const spFrom = queryParams.get('spFrom');
    const spUpto = queryParams.get('spUpto');
    const soldFrom = queryParams.get('soldFrom');
    const soldUpto = queryParams.get('soldUpto');
    if (barcode) {
        searchFilterObject = {
            filter: 'barcode',
            barcode: barcode
        }
    } else {
        searchFilterObject = {
            filter: 'multiple',
            filters: {
                is_applied_name: itemName ? true : false,
                exact_name: matchExactly ? true : false,
                name: itemName,
                is_applied_weight_from: weightFrom ? true : false,
                weight_from: weightFrom,
                is_applied_weight_upto: weightUpto ? true : false,
                weight_upto: weightUpto,
                is_applied_average_cost_price_from: cpFrom ? true : false,
                average_cost_price_from: cpFrom,
                is_applied_average_cost_price_upto: cpUpto ? true : false,
                average_cost_price_upto: cpUpto,
                is_applied_stock_from: stockFrom ? true : false,
                stock_from: stockFrom,
                is_applied_stock_upto: stockUpto ? true : false,
                stock_upto: stockUpto,
                is_applied_sold_from: soldFrom ? true : false,
                sold_from: soldFrom,
                is_applied_sold_upto: soldUpto ? true : false,
                sold_upto: soldUpto,
                is_applied_sales_price_from: spFrom ? true : false,
                sales_price_from: spFrom,
                is_applied_sales_price_upto: spUpto ? true : false,
                sales_price_upto: spUpto,
                is_applied_category: category ? true : false,
                category: category
            }
        };
    }
    return searchFilterObject;
}

export const getInvoicesSearchFilterObject = (props) => {
    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString) || '';
    let searchFilterObject = {}
    //return empty of no query string
    if (!isQueryStringSearchable(queryString)) {
        return searchFilterObject
    }
    let invoiceNo = queryParams.get('invoiceNo') || '';
    let dateFrom = queryParams.get('dateFrom') ? new Date(queryParams.get('dateFrom')) : '';
    let dateUpto = queryParams.get('dateUpto') ? new Date(queryParams.get('dateUpto')) : '';
    let customer = queryParams.get('customer') || '';
    // let customerName = queryParams.get('customerName') || '';
    let invoiceStatus = queryParams.get('invoiceStatus') || '';
    let isActive = queryParams.get('isActive') || '';
    let isCanceled = queryParams.get('isCanceled') || '';
    if (invoiceNo) {
        searchFilterObject = {
            'filter': 'invoice_number',
            'invoice_number': invoiceNo
        }
    } else {
        searchFilterObject = {
            filter: 'multiple',
            filters: {
                "date": (dateFrom || dateUpto) ? true : false,
                'start_date': dateFrom ? dateFrom.toISOString() : '',
                'end_date': dateUpto ? dateUpto.toISOString() : '',
                "customer": customer ? true : false,
                'customer_id': customer,
                'status': invoiceStatus ? true : false,
                'status_id': invoiceStatus,
                'active': isActive ? {
                    is_active: isActive === 'true' ? true : false
                } : null,
                'canceled': isCanceled ? {
                    'is_canceled': isCanceled === 'true' ? true : false
                } : null
            }
        };
    }
    return searchFilterObject;
}


