export const getAccountsSearchFilterObject = (props) => {
    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString) || '';
    let searchFilterObject = {}
    //return empty of no query string
    if (!isQueryStringSearchable(queryString)) {
        return searchFilterObject
    }
    let accountName = queryParams.get('accountName') || false;
    let status = queryParams.get('status') || '';
    let childOf = queryParams.get('childOf') || '';
    // let childOfName = queryParams.get('childOfName') || '';
    let vendor = queryParams.get('vendor') || false;
    // let vendorName = queryParams.get('vendorName') || '';
    let customer = queryParams.get('customer') || false;
    // let customerName = queryParams.get('customerName') || '';
    let currentAmountFrom = queryParams.get('currentAmountFrom') || '';
    let currentAmountUpto = queryParams.get('currentAmountUpto') || '';
    let creditsFrom = queryParams.get('creditsFrom') || '';
    let creditsUpto = queryParams.get('creditsUpto') || '';
    let isParent = queryParams.get('isParent') || false;
    let accountType = queryParams.get('accountType') || false;
    let accountHeader = queryParams.get('accountHeader') || false;



    searchFilterObject = {
        filter: 'multiple',
        filters: {
            status: status === '' ? false : true, //status:true-> either closed or open, status:false->any
            closed: status === 'true' ? true : false, //[status on right is fontend variable]
            name: accountName,
            vendor: vendor,
            customer: customer,
            current_amount: (currentAmountFrom || currentAmountUpto) ? true : false,
            current_amount_from: currentAmountFrom,
            current_amount_upto: currentAmountUpto,
            credits: (creditsFrom || creditsUpto) ? true : false,
            credits_from: creditsFrom,
            credits_upto: creditsUpto,
            parent: isParent,
            children: childOf ? true : false,
            parent_id: childOf,
            account_type: accountType,
            header: accountHeader,
        }

    }
    return searchFilterObject;
}


