export const getLedgerEntriesSearchFilterObject = (props) => {
    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString) || '';
    let searchFilterObject = {};
    //return empty of no query string
    if (!isQueryStringSearchable(queryString)) {
        return searchFilterObject;
    }
    let bundleId = queryParams.get('bundleId') || false;
    let account = queryParams.get('account') || false;
    // let accountName = queryParams.get('accountName') || '';
    let dateFrom = queryParams.get('dateFrom') ? new Date(queryParams.get('dateFrom')) : '';
    let dateUpto = queryParams.get('dateUpto') ? new Date(queryParams.get('dateUpto')) : '';
    let isAdd = queryParams.get('isAdd') || '';
    let amountFrom = queryParams.get('amountFrom') || '';
    let amountUpto = queryParams.get('amountUpto') || '';
    let paymentMethod = queryParams.get('paymentMethod') || false;

    searchFilterObject = {
        filter: 'multiple',
        filters: {
            account: parseInt(account),
            "date": (dateFrom || dateUpto) ? true : false,
            'start_date': dateFrom ? dateFrom.toISOString() : null,
            'end_date': dateUpto ? dateUpto.toISOString() : null,
            bundle_id: parseInt(bundleId),
            apply_is_add: isAdd === '' ? false : true,
            is_add: isAdd === 'true' ? true : false,
            amount: (amountFrom || amountUpto) ? true : false,
            amount_from: amountFrom,
            amount_upto: amountUpto,
            payment_method: paymentMethod,
        }
    }
    return searchFilterObject;
}


