export const getUsersSearchFilterObject = (props) => {
    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString) || '';
    let searchFilterObject = {}
    //return empty of no query string
    if (!isQueryStringSearchable(queryString)) {
        return searchFilterObject
    }

    let name = queryParams.get('name') || '';
    let role = queryParams.get('role') || '';
    let status = queryParams.get('status') || '';

    if (name) {
        searchFilterObject.filter = 'name'
        searchFilterObject.name = name
    }


    searchFilterObject = {
        filter: 'multiple',
        filters: {
            name: name,
            role_id: role,
            role: role,
            status: status ? true : false, //true if is_active is there
            is_active: status === 'true' ? true : false
        }
    }
    return searchFilterObject;
}




