import React from 'react';
import { withRouter } from 'react-router-dom'
import { Pagination, Form, InputGroup, Row } from 'react-bootstrap'

function Paging(props) {


    const queryString = props.location.search;
    const queryParams = new URLSearchParams(queryString);
    let start = parseInt(queryParams.get('start') || 0);
    let end = parseInt(queryParams.get('end') || 25);

    const indexQuotient = parseInt(start / (end - start));
    const [instancePerPage] = React.useState(end - start);
    const [activePageIndex, setActivePageIndex] = React.useState(indexQuotient);
    // const [start, setStart] = React.useState(0)
    // const [end, setEnd] = React.useState(25);


    React.useEffect(() => {

        //todo here

        // check if the query string is okay
        //if not change to valid one
        //okay? so replace state.



        // console.log('start', start, 'end', end);



        const queryNotValid =
            start % 25 !== 0 ||
            end % 25 !== 0 ||
            (end - start !== 25 && end - start !== 50 && end - start !== 100) ||
            end < 25 ||
            start < 0

        if (queryNotValid) {
            start = 0;
            end = 25;
            queryParams.set('start', 0)
            queryParams.set('end', 25)
            props.history.push({ search: queryParams.toString() })
        }
    }, [])


    const _pageNoClick = (pageIndex) => {
        setActivePageIndex(pageIndex);

        queryParams.set('start', instancePerPage * pageIndex)
        queryParams.set('end', instancePerPage * (pageIndex + 1))

        props.history.push({
            search: queryParams.toString()
        })
        console.log(pageIndex)
    }

    const _change = async (e) => {
        queryParams.set('start', 0)
        queryParams.set('end', parseInt(e.target.value))
        props.history.push({
            search: queryParams.toString()
        })
    }

    // console.log('active page index', activePageIndex)
    const instanceCount = props.instanceCount;
    let noOfPages = Math.ceil(instanceCount / instancePerPage);


    if (activePageIndex >= noOfPages - 5) {
        var dynamicPageArr = [
            noOfPages - 5,
            noOfPages - 4,
            noOfPages - 3,
            noOfPages - 2,
        ]
    } else {
        var dynamicPageArr = [
            activePageIndex - 1,
            activePageIndex - 0,
            activePageIndex + 1,//active page number
            activePageIndex + 2,
            activePageIndex + 3,
        ]
    }
    // } else {
    //     var dynamicPageArr = [
    //         activePageIndex - 4,
    //         activePageIndex - 3,
    //         activePageIndex - 2,
    //         activePageIndex - 1,
    //     ]
    // }

    return (
        <Row style={{ display: 'flex', justifyContent: 'space-between', padding: '40px 10px 0 10px   ' }}>
            <InputGroup size="sm" className="mb-3" style={{ maxWidth: '120px', backgroundColor: 'white' }}>
                <InputGroup.Prepend>
                    <InputGroup.Text id="inputGroup-sizing-sm">Show:</InputGroup.Text>
                </InputGroup.Prepend>
                <Form.Control
                    style={{ height: '30.21px', backgrondColor: 'white' }}
                    as="select"
                    value={instancePerPage}
                    onChange={_change}
                >
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                </Form.Control>
            </InputGroup>


            <Pagination style={{ marginBottom: 0 }} size='sm'>

                <Pagination.Prev
                    onClick={() => _pageNoClick(activePageIndex - 1)}
                    disabled={activePageIndex === 0}
                />
                {noOfPages <= 8 ?
                    [1, 2, 3, 4, 5, 6, 7, 8].map((el, index) => {
                        if (el > noOfPages) return;
                        return <Pagination.Item
                            disabled={noOfPages < el}
                            key={index}
                            active={activePageIndex === el - 1}
                            onClick={() => _pageNoClick(el - 1)}
                        >{el}</Pagination.Item>
                    }) :
                    <>
                        {activePageIndex > 3 ?
                            <>
                                < Pagination.Item
                                    active={activePageIndex === 0}
                                    onClick={() => _pageNoClick(0)}
                                >{1}</Pagination.Item><Pagination.Item
                                    active={activePageIndex === 1}
                                    onClick={() => _pageNoClick(1)}
                                >{2}</Pagination.Item>
                                <Pagination.Ellipsis disabled={true} />

                            </>
                            : null
                        }

                        {
                            (activePageIndex > 3 ?
                                dynamicPageArr :
                                [1, 2, 3, 4, 5]).map((el, index) => {
                                    return <Pagination.Item
                                        disabled={noOfPages < el}
                                        key={index}
                                        active={activePageIndex === el - 1}
                                        onClick={() => _pageNoClick(el - 1)}
                                    >{el}</Pagination.Item>
                                })
                        }
                        {noOfPages > 7 && (activePageIndex < noOfPages - 5) ? <Pagination.Ellipsis disabled={true} /> : null}
                        {noOfPages > 7 ?
                            <>
                                < Pagination.Item
                                    active={activePageIndex === noOfPages - 2}
                                    onClick={() => _pageNoClick(noOfPages - 2)}
                                >{noOfPages - 1}</Pagination.Item><Pagination.Item
                                    active={activePageIndex === noOfPages - 1}
                                    onClick={() => _pageNoClick(noOfPages - 1)}
                                >{noOfPages}</Pagination.Item>
                            </>
                            : <>
                                < Pagination.Item
                                    disabled={noOfPages < 6}
                                    active={activePageIndex === 7 - 2}
                                    onClick={() => _pageNoClick(7 - 2)}
                                >{6}</Pagination.Item><Pagination.Item
                                    disabled={noOfPages < 7}
                                    active={activePageIndex === 7 - 1}
                                    onClick={() => _pageNoClick(7 - 1)}
                                >{7}</Pagination.Item>
                            </>
                        }
                    </>
                }

                <Pagination.Next
                    onClick={() => _pageNoClick(activePageIndex + 1)}

                    disabled={(activePageIndex === noOfPages - 1) || noOfPages === 0}
                />
            </Pagination>
        </Row >
    )
}

export default withRouter(Paging)
