import React from 'react';
import { Row, Col, Card, Table, Modal, Button, OverlayTrigger, Tooltip, Image, Form } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";
import DEMO from '../../globals/constant';

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import VendorsFilter from '../../App/components/SearchFilters/VendorsFilter';
import { getVendorsSearchFilterObject, getUsersSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';
import { getUsers, toggleUserActivity } from '../../api/userManagement/users';
import { getRoles, assignRole } from '../../api/userManagement/roles';
import { connect } from 'react-redux';
import UsersFilter from '../../App/components/SearchFilters/UsersFilter';

const Users = (props) => {
    const [loading, setLoading] = React.useState(true)
    const [usersArr, setUsersArr] = React.useState([]);
    const [rolesArr, setRolesArr] = React.useState([])
    const [toggleActivityData, setToggleActivityData] = React.useState({ showModal: false, user: {} })
    const [changeRoleData, setChangeRoleData] = React.useState({ userId: null, roleId: '', showModal: false })
    const [instanceCount, setInstanceCount] = React.useState(0)


    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getUsersSearchFilterObject(props),
            ...getStartEnd(props),
        }
        console.log(JSON.stringify(requestJson))
        getUsers(requestJson, controller.signal)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    setUsersArr(res.json.users);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        getRoles({ "action": "get", "filter": "none", "start": 0, "end": 999999 }, controller.signal)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    await setRolesArr(res.json.roles);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, []);



    const _triggerToggleActivityModal = (user) => {
        setToggleActivityData({ showModal: true, user: user });
    }

    const _triggerChangeRoleModal = (userId) => {
        setChangeRoleData({ ...changeRoleData, userId: userId, showModal: !changeRoleData.showModal })
    }

    const _roleChange = (e) => {
        console.log(e.target.value)
        setChangeRoleData({ ...changeRoleData, roleId: e.target.value })
    }
    const _updateRole = async () => {
        const requestJson = {
            'action': 'assign',
            'user_id': changeRoleData.userId,
            'role_id': changeRoleData.roleId
        }
        console.log(JSON.stringify(requestJson))
        assignRole(requestJson)
            .then(res => {
                console.log(res)
                if (res.json.status) {
                    let tempArr = [...usersArr];
                    console.log(rolesArr)
                    const selectedRole = rolesArr.find(role => {
                        return role.id === parseInt(changeRoleData.roleId)
                    });
                    for (let i in tempArr) {
                        if (tempArr[i].id === changeRoleData.userId) {
                            tempArr[i].role_str = selectedRole.name;
                            tempArr[i].role = selectedRole.id;
                        }
                    }
                    setUsersArr(tempArr)
                    setChangeRoleData({ ...changeRoleData, showModal: false, userId: null, roleId: '' });
                    Jhilikka.fire({ icon: 'success', title: `Role of the user changed successfully!`, background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }

            })
    }

    const _toggleActivity = async () => {
        const user = toggleActivityData.user
        const requestJson = {
            'action': user.is_active ? 'deactivate' : 'activate',
            'user_id': user.id
        }
        console.log(requestJson)
        toggleUserActivity(requestJson)
            .then(res => {
                console.log(res);
                if (res.json.status) {
                    let tempArr = [...usersArr];
                    for (let i in tempArr) {
                        if (tempArr[i].id === user.id) {
                            tempArr[i].is_active = !user.is_active;
                        }
                    }
                    setUsersArr(tempArr)
                    setToggleActivityData({ ...setToggleActivityData, showModal: false, user: {} })
                    Jhilikka.fire({ icon: 'success', title: `User ${user.is_active ? 'deactivated' : 'activated'} successfully!`, background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => {
                console.log(err)
            })
    }


    const UsersList = usersArr.map((user, index) => {
        const userProfile = user.profile ? user.profile : {};
        return (
            <tr key={index}>
                <th scope="row">{user.id}</th>
                <td className="imageTd">
                    <Image
                        src={userProfile.profile_image ? `${DEMO.SERVER_URL}${userProfile.profile_image}` : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTcZsL6PVn0SNiabAKz7js0QknS2ilJam19QQ&usqp=CAU"}
                        thumbnail={true}
                        style={{ height: '46px', minWidth: '46px', width: '46px', objectFit: 'cover' }}
                        alt="..." />
                </td>
                <td
                    onClick={() => {
                        props.history.push('/user/' + user.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{user.name}{parseInt(user.id) === props.auth.user.user_id ? " (You)" : null}</td>
                <td>{user.role_str}</td>
                <td>{user.email}</td>
                <td>{user.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <span
                        className='rowIconButton'
                        onClick={() => _triggerToggleActivityModal(user)}
                    >
                        <OverlayTrigger
                            key={'top'}
                            placement={'top'}
                            overlay={
                                <Tooltip id={`tooltip-${'top'}`}>{user.is_active ? 'Deactivate' : 'Activate'} User</Tooltip>
                            }
                        >
                            <i
                                className={user.is_active ? "feather icon-user-x text-c-red f-19 m-0" : "feather icon-user-check text-c-blue f-19 m-r-5"}
                                style={{ cursor: 'pointer' }}
                            />
                        </OverlayTrigger>
                    </span>
                    <span
                        className='rowIconButton'
                        onClick={() => _triggerChangeRoleModal(user.id)}
                    >
                        <OverlayTrigger
                            key={'top'}
                            placement={'top'}
                            overlay={<Tooltip id={`tooltip-top`}>Assign different role</Tooltip>}
                        >
                            <i
                                className={"feather icon-user-plus text-c-blue f-19 m-0"}
                                style={{ cursor: 'pointer' }}
                            />
                        </OverlayTrigger>
                    </span>

                </td>
            </tr>
        )
    })

    return (
        <Aux>

            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" >Users</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`user/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>

            </Row>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <UsersFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Photo</th>
                                        <th>Name</th>
                                        <th>Role</th>
                                        <th>Email</th>
                                        <th>Active</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {UsersList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(usersArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>
                    </Card >



                    {/*--------------- Activate and Deactivate of user---------------- */}
                    <Modal
                        style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                        show={toggleActivityData.showModal}
                        onHide={() => setToggleActivityData({ ...toggleActivityData, showModal: false, user: {} })}
                    >
                        <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                            <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Are you sure?</Modal.Title>
                        </Modal.Header>

                        <Modal.Body
                            style={{ paddingLeft: '30px' }}
                        >
                            User will be {toggleActivityData.user.is_active ? 'deactivated' : 'activated'}
                        </Modal.Body>


                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                            <Button variant="secondary" onClick={() => setToggleActivityData({ ...toggleActivityData, showModal: false, user: {} })}>
                                Cancel
                            </Button>
                            {toggleActivityData.user.is_active ?
                                <Button variant="danger" onClick={_toggleActivity}>
                                    Deactivate
                                </Button> :
                                <Button variant="primary" onClick={_toggleActivity}>
                                    Activate
                                </Button>
                            }
                        </div>
                    </Modal>

                    {/*--------------- Change Role of user---------------- */}
                    <Modal
                        centered={true}
                        style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                        show={changeRoleData.showModal}
                        onHide={() => setChangeRoleData({ ...changeRoleData, showModal: false, userId: null, roleId: '' })}
                    >
                        <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                            <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Choose Role</Modal.Title>
                        </Modal.Header>

                        <Modal.Body
                            style={{ paddingLeft: '30px' }}
                        >
                            <Col md={12}>
                                <Form.Group >
                                    <Form.Label>Role*</Form.Label>
                                    <Form.Control name="role" value={changeRoleData.roleId} onChange={_roleChange} as="select">

                                        <option value='' disabled >Select User Role</option>
                                        {rolesArr.map((instance, idex) => {
                                            return <option key={idex} value={instance.id}>{instance.name}</option>
                                        })}
                                    </Form.Control>
                                </Form.Group>
                            </Col></Modal.Body>


                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                            <Button variant="secondary" onClick={() => setChangeRoleData({ ...changeRoleData, showModal: false, userId: null, roleId: '' })}>
                                Cancel
                            </Button>
                            {changeRoleData.roleId ?
                                <Button variant="primary" onClick={_updateRole}>
                                    Done
                                </Button> :
                                null}

                        </div>
                    </Modal>
                </Col>
            </Row>
        </Aux >
    );
}

const mapStateToProps = state => ({
    auth: state.auth
})

export default connect(mapStateToProps)(Users);